import React from "react";
import { graphql, navigate } from "gatsby";
import { Layout, Resume } from "./../components";

const ResumePage = ({
  data: {
    site: { siteMetadata: metadata }
  }
}) => {
  return (
    <Layout
      title="Resume"
      metadata={metadata}
      titleLinkHandler={() => navigate("/")}
    >
      <Resume metadata={metadata} />
    </Layout>
  );
};

export default ResumePage;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        description
        year
        author {
          name
          avatar
          bio
          social {
            phone
            email
            linkedin
          }
        }
        resume {
          description
          skills {
            name
            level
          }
          timeline {
            name
            events {
              title
              subtitle
              type
              startDate(formatString: "MMMM YYYY")
              endDate(formatString: "MMMM YYYY")
              points
            }
          }
        }
      }
    }
  }
`;
