import React from "react";
import { Flex, FlexItem, Icon } from "./../../design-system";
import styled from "styled-components";

const Resume = ({ metadata }) => (
  <section data-id="resume" data-title="Resume">
    <Flex className="d-print-none">
      <FlexItem>
        <h1>Hi,</h1>
        <br />
      </FlexItem>
    </Flex>
    <Flex flexWrap="wrap">
      <FlexItem pr={[0, 0, 0, 4]} width={[1, 1, 1, 3 / 4]}>
        <FlexItem>
          <div dangerouslySetInnerHTML={{ __html: metadata.author.bio }} />
          <div
            dangerouslySetInnerHTML={{ __html: metadata.resume.description }}
          />
          <br />
        </FlexItem>
        <FlexItem>
          <Timeline>
            {metadata.resume.timeline.map(type => (
              <div key={type.name}>
                <h3>{type.name}</h3>
                <br />
                {type.events.map((event, index) => (
                  <div key={index}>
                    <ul className="timeline">
                      <li key={event.title}>
                        {event.startDate && (
                          <span className="timeline_time">
                            {event.startDate} -{" "}
                            {event.endDate ? event.endDate : "Present"}
                          </span>
                        )}
                        <div className="timeline_icon">
                          <Icon name={event.type} />
                        </div>
                        <div className="timeline_content">
                          <h5 style={{ marginBottom: 2 }}>{event.title}</h5>
                          <strong>{event.subtitle}</strong>
                          {event.overview && <div>{event.overview}</div>}
                          {event.points && (
                            <ul>
                              {event.points.map((point, index) => (
                                <li
                                  key={index}
                                  dangerouslySetInnerHTML={{ __html: point }}
                                />
                              ))}
                            </ul>
                          )}
                        </div>
                      </li>
                    </ul>
                    <br />
                  </div>
                ))}
              </div>
            ))}
          </Timeline>
        </FlexItem>
      </FlexItem>
      <FlexItem width={[1, 1, 1, 1 / 4]}>
        <h3>Skills</h3>
        <br />
        <Flex flexWrap="wrap">
          {metadata.resume.skills.map(skill => (
            <FlexItem
              pr={[0, 3, 3, 0]}
              mb={3}
              width={[1, 1 / 2, 1 / 2, 1]}
              key={skill.name}
            >
              <strong>{skill.name}</strong>
              <div className="progress">
                <div
                  className="progress-bar bg-primary"
                  style={{ width: `${skill.level}%` }}
                />
              </div>
            </FlexItem>
          ))}
        </Flex>
      </FlexItem>
    </Flex>
  </section>
);

export default Resume;

const Timeline = styled.div`
  .timeline {
    margin: 0;
    padding: 0;
    list-style: none;
    position: relative;
  }

  .timeline:before {
    display: none;
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    width: 3px;
    background: ${props => props.theme.colors.secondary};
    left: 20%;
    margin-left: -6px;
  }

  .timeline > li .timeline_time {
    display: block;
    position: relative;
    padding: 0 0 20px 0;
  }

  .timeline > li .timeline_content {
    background: ${props => props.theme.colors.secondary};
    position: relative;
    border-radius: 5px;
    padding: 1em;
  }

  .timeline > li .timeline_icon {
    position: relative;
    float: right;
    left: auto;
    text-align: center;
    margin: -10px -10px 0px 0px;
    width: 40px;
    height: 40px;
    line-height: 40px;
    font-size: 1rem;
    z-index: 9999;
    color: ${props => props.theme.colors.background};
    background: ${props => props.theme.colors.primary};
    border-radius: 50%;
    box-shadow: 0 0 0 5px ${props => props.theme.colors.secondary};
  }

  @media screen {
    .timeline > li .timeline_content:after {
      right: auto;
      border: solid transparent;
      content: " ";
      height: 0;
      width: 0;
      left: 20px;
      position: absolute;
      pointer-events: none;
      border-right-color: transparent;
      border-bottom-color: ${props => props.theme.colors.secondary};
      border-width: 10px;
      top: -20px;
    }
  }

  @media screen and (min-width: 1200px) {
    .timeline:before {
      display: block;
    }

    .timeline > li .timeline_icon {
      position: absolute;
      left: 20%;
      top: 0;
      margin: 0 0 0 -25px;
    }

    .timeline > li .timeline_time {
      text-align: right;
      width: 23%;
      padding-right: 9%;
      position: absolute;
    }

    .timeline > li .timeline_content {
      margin: 0 0 15px 25%;
    }

    .timeline > li .timeline_content:after {
      right: 100%;
      left: auto;
      border-right-color: ${props => props.theme.colors.secondary};
      border-bottom-color: transparent;
      top: 10px;
    }
  }
`;
