import React from "react";
import Helmet from "react-helmet";
import { Flex, FlexItem } from "./../../design-system";
import Social from "./Social";
import {
  PageContainer,
  PageContentContainer,
  PageContent,
  PageSidebarContainer,
  PageSidebarContent,
  PageSectionContainer,
  PageSectionTitle,
  PageSubline,
  PageFooter
} from "./styled/styled-page";
import styled from "styled-components";

export const Avatar = styled.img`
  width: 170px;

  @media screen and (min-width: 768px) {
    width: 216px;
  }
`;

const Layout = ({
  title,
  header,
  subheader,
  metadata,
  children,
  titleLinkHandler
}) => {
  return (
    <PageContainer>
      {title && <Helmet title={`${title} | ${metadata.title}`} />}
      <PageSidebarContainer className="primary-linear-gradient">
        <PageSidebarContent>
          <Flex
            flexDirection="column"
            mr={[0, "3rem"]}
            justifyContent={["center", "center", "left"]}
          >
            <FlexItem mb={[2]}>
              <Avatar
                className="img-fluid rounded"
                src={metadata.author.avatar}
                alt="Avatar"
              />
            </FlexItem>
            <FlexItem mb={[1]}>
              <h1>
                <a href="#" onClick={() => titleLinkHandler()}>
                  {metadata.title}
                </a>
              </h1>
            </FlexItem>
            <FlexItem>
              <p
                className="lead"
                dangerouslySetInnerHTML={{ __html: metadata.description }}
              />
            </FlexItem>
            <FlexItem>
              <Social social={metadata.author.social} />
            </FlexItem>
          </Flex>
        </PageSidebarContent>
      </PageSidebarContainer>
      <PageContentContainer>
        <PageContent>
          {(header || subheader) && (
            <PageSectionContainer>
              {header && <PageSectionTitle>{header}</PageSectionTitle>}
              {subheader && <PageSubline>{subheader}</PageSubline>}
            </PageSectionContainer>
          )}
          {children}
        </PageContent>
        <PageFooter>
          &copy; {metadata.year} by {metadata.author.name}
          . All rights reserved. <br />
        </PageFooter>
      </PageContentContainer>
    </PageContainer>
  );
};

export default Layout;
