import styled from "styled-components";
import { Box } from "./Box";
import {
  alignItems,
  justifyContent,
  alignContent,
  flexWrap,
  flexDirection,
  order,
  // flexGrow,
  // flexShrink,
  flexBasis,
  flex,
  alignSelf
} from "styled-system";

export const Flex = styled(Box)(
  { display: "flex" },
  alignItems,
  justifyContent,
  alignContent,
  flexWrap,
  flexDirection
);

Flex.displayName = "Flex";

Flex.propTypes = {
  ...alignItems.propTypes,
  ...justifyContent.propTypes,
  ...alignContent.propTypes,
  ...flexWrap.propTypes,
  ...flexDirection.propTypes
};

export const InlineFlex = styled(Flex)({ display: "inline-flex" });

InlineFlex.displayName = "InlineFlex";

InlineFlex.propTypes = {};

export const FlexItem = styled(Box)(
  {
    boxSizing: "border-box"
  },
  order,
  flexBasis,
  flex,
  alignSelf
);

FlexItem.displayName = "FlexItem";

FlexItem.propTypes = {
  ...order.propTypes,
  ...flexBasis.propTypes,
  ...flex.propTypes,
  ...alignSelf.propTypes
};
